import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'

import Index from '@/pages/Index'
import Fenlei from '@/pages/Fenlei'
import Shop from '@/pages/Shop'
import Wd from '@/pages/Wd'
import Newjx from '@/pages/Newjx'

import Fenleixq from '@/pages/Fenleixq'
import Spxq from '@/components/Spxq'

import Zhuce from '@/pages/Zhuce'
import Denglu from '@/pages/Denglu'


import Order from '@/components/Order'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:"/",
      redirect: "/home/index"
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      
      children:[
        {
          path:"index",
          component:Index,
        },
        {
          path:"fenlei",
          component: Fenlei,
        },
        {
          path:"shop",
          component: Shop,
        },
        {
          path:"wd",
          component: Wd,
        }
      ]
    },
    {
      path:"/newjx",
      name:"newjx",
      component:Newjx
    },
    {
      path:"/newjx/:id",
      name:"newjx1",
      component:Newjx
    },
    {
      path:"/zhuce",
      name:"zhuce",
      component:Zhuce
    },
    {
      path:"/denglu",
      name:"Denglu",
      component:Denglu
    },
    {
      path:"/fenleixq",
      name:"fenleixq",
      component:Fenleixq,
    },
    {
      path:"/spxq",
      name:"Spxq",
      component:Spxq
    },
    {
      path:"/order",
      name:"Order",
      component:Order
    },
  ]
})



