<template>
  <div class="zc">
    <div class="wrap">
      <h4>新用户注册</h4>
      <p class="p">很高兴您将成为商城的会员(注册只需要一步)</p>
      <p class="zcp1" :class="{'action':phone,'action1':phone1}">
        <input type="text" placeholder="手机号" v-model="tel" @blur="sjh" />
        <span v-show="bitian">必填</span>
        <van-icon class="van" name="graphic" />
      </p>
      <p class="zcp1" :class="{'action':pwd0,'action1':pwd1}">
        <input type="password" placeholder="密码" v-model="pwd" v-if="yj" @blur="zcmima" />
        <input type="text" placeholder="密码" v-model="pwd" v-else />
        <van-icon class="van" name="closed-eye" v-if="yj" @click="mima" />
        <van-icon class="van" name="eye-o" v-else @click="mm" />
        <span v-show="bit">必填</span>
      </p>
      <p class="zcp1" :class="{'action':qr0,'action1':qr1}">
        <input type="password" placeholder="确认密码" v-model="qrpwd0" v-if="qryj" @blur="qrmima" />
        <input type="text" placeholder="确认密码" v-model="qrpwd0" v-else />
       <span v-show="bt">必填</span>
        <van-icon class="van" name="closed-eye" v-if="qryj" @click="qrmm" />
        <van-icon class="van" name="eye-o" v-else @click="qrmmia" />
      </p>
      <p class="zcp1">
        <input type="text" placeholder="用户名" />
        <span style="display:none">必填</span>
        <van-icon class="van" name="manager" />
      </p>
      <p class="wrapimg">
        <input type="text" v-model="tyzm" />
        <img :src="code +key " @click="upImg" />
        <!-- <img :src="code" alt @click="upImg" /> -->
      </p>
      <p class="pk">
        <van-field
          readonly
          clickable
          label="城市"
          :value="value"
          placeholder="选择城市"
          @click="showPicker = true"
        />
      </p>
      <van-popup v-model="showPicker" round position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
      <p class="pyzm">
        <input type="text" v-model="yzm" />
        <span class="span1" @click="get" v-if="yzmshij">获取验证码</span>
        <span class="span1" v-else>{{ sj }}秒后再试</span>
        <span class="span2" style="display:none"></span>
      </p>
      <p class="ljzc" @click="zhuce">立即注册</p>
      <p class="pw">已有账户？ 立即登录</p>
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from "uuid";
import qs from "qs";
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //手机号
      phone: false,
      phone1: false,
      bitian: false,
      tel: "",

      //密码
      pwd0: false,
      pwd1: false,
      bit: false,
      pwd: "",


      //确认密码
      qrpwd0:"",
      qrpwd: false,
      qr0: false,
      qr1: false,
      bt: false,
      pr: "",
      //模拟数据
      code: "https://api.it120.cc/small4/verification/pic/get?key=",
      //开启图形验证码必填
      key: uuidv4(),

      //图形验证码
      tyzm: "",

      yzm: "",
      yj: true,
      qryj:true,
      sj: 60,
      yzmshij: true,
      timer: null,

      //城市数据
      value: "",
      showPicker: false,
      columns: ["杭州", "宁波", "温州", "嘉兴", "湖州", "山西"]
    };
  },
  created() {
    //创建后
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    //图形验证码
    upImg() {
      //   this.code =
      //     "https://api.it120.cc/small4/verification/pic/get?key=" + uuidv4();

      this.key = uuidv4();
    },
    //获取验证码
    //短信验证码
    get() {
      var data = { mobile: this.tel, picCode: this.tyzm, key: this.key };

      this.$http
        .post(
          "https://api.it120.cc/small4/verification/sms/get",
          qs.stringify(data)
        )
        .then(res => {
          console.log(res);
        });
      //倒计时
      this.yzmshij = false;
      this.timer = setInterval(() => {
        if (this.sj > 0 && this.sj <= 60) {
          this.sj--;
        } else {
          this.yzmshij = true;
          clearInterval(this.timer);
          this.sj = 60;
          this.timer = null;
        }
      }, 1000);
    },
    //立即注册
    zhuce() {
      var data = { mobile: this.tel, pwd: this.pwd, code: this.yzm };

      this.$http
        .post("https://api.it120.cc/small4/user/m/register", qs.stringify(data))
        .then(res => {
          console.log(res);
        });
    },
    //密码隐藏
    mima() {
      this.yj = false;
    },
    //密码显示
    mm() {
      this.yj = true;
    },
    //密码隐藏
    qrmm(){
      this.qryj = false;
    },
    //密码显示
    qrmmia() {
      this.qryj = true;
    },
     //手机号  失去焦点
    sjh() {
      if (this.tel == "") {
        this.phone = true;
        this.bitian = true;
        this.$notify({ type: "warning", message: "请输入手机号" });
      } else if (this.tel != "") {
        this.phone1 = true;
        this.bitian = false;
      }
    },
    //密码  失去焦点
    zcmima() {
      if (this.pwd == "") {
        this.pwd0 = true;
        this.bit = true;
        this.$notify({ type: "warning", message: "请输入密码" });
      } else if (this.pwd != "") {
        this.pwd1 = true;
        this.bit = false;
      }
    },
    //确认密码  失去焦点
    qrmima() {
      if (this.qrpwd == "") {
        this.qr0 = true;
        this.bt = true;
        this.$notify({ type: "warning", message: "请确认密码" });
      } else if (this.qrpwd != "") {
        this.qr1 = true
        this.bt = false;
      }
    },

    //城市数据
    onConfirm(value) {
      this.value = value;
      this.showPicker = false;
    }
  }
};
</script>

<style scoped lang="less">
.action {
  border: 0.01rem solid rgb(225, 44, 30) !important;
}
.action1 {
  border: 0.01rem solid rgb(56, 219, 36) !important;
  background: rgb(225, 255, 223) !important;
  input {
    background: rgb(225, 255, 223) !important;
  }
}
.zc {
  padding: 0.2rem;
  .wrap {
    padding: 0 0.2rem;
    height: 10rem;
    min-height: 10rem;
    border: 0.02rem solid #f5f5f5;
    h4 {
      font-size: 0.3rem;
      margin-top: 0.4rem;
    }
    .p {
      font-size: 0.2rem;
      color: #909090;
      margin-top: 0.5rem;
      margin-bottom: 0.23rem;
    }
    .zcp1 {
      //   width: 2.8rem;
      height: 0.6rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.44rem;
      display: flex;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      input {
        margin-left: 0.2rem;
        width: 2.3rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        color: #bfbfbf;
      }
      span {
        font-size: 0.16rem;
        color: #e12c1e;
      }
      .van {
        font-size: 0.22rem;
        color: #d1cfce;
        position: absolute;
        left: 2.85rem;
        top: 0.24rem;
      }
    }
    .wrapimg {
      //   width: 2.8rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.44rem;
      display: flex;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      input {
        margin-left: 0.2rem;
        width: 1.5rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
      }

      img {
        width: 1.5rem;
        height: 0.6rem;
        position: absolute;
        right: 0;
        top: 0;
        // background-color: yellowgreen;
      }
    }
    .pk {
      //   width: 2.8rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      font-size: 0.2rem;
    }
    .pyzm {
      //   width: 2.8rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      font-size: 0.2rem;
      input {
        margin-left: 0.2rem;
        width: 1.7rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
      }
      .span1 {
        display: block;
        width: 1.7rem;
        height: 0.65rem;
        border-left: 0.01rem solid #e1e1e1;
        font-size: 0.16rem;
        text-align: center;
        line-height: 0.65rem;
      }
      .span2 {
        width: 1.7rem;
        height: 0.65rem;
        border-left: 0.01rem solid #e1e1e1;
        font-size: 0.2rem;
        text-align: center;
        line-height: 0.65rem;
      }
    }
    .ljzc {
      height: 0.57rem;
      border-radius: 0.06rem;
      background: -webkit-gradient(
        linear,
        left top,
        right top,
        from(#5ea6f8),
        to(#bb87f6)
      );
      background: linear-gradient(90deg, #5ea6f8, #bb87f6);
      text-align: center;
      line-height: 0.57rem;
      font-size: 0.2rem;
      color: white;
      letter-spacing: 0.06rem;
      margin: 0 auto;
      margin-top: 0.24rem;
    }
    .pw {
      height: 0.5rem;
      margin-top: 0.2rem;
      font-size: 0.18rem;
      color: dodgerblue;
      text-align: center;
    }
  }
}
</style>



