<template>
  <div id="spxq">
    <div class="xqlb">
      <van-swipe class="my-swipe" :autoplay="3000">
        <van-swipe-item v-for="(item,index) in banner" :key="index">
          <img class="lbimg" :src="item.pic" alt />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="fan">
      <p class="p1">
        <van-icon name="arrow-left" @click="fenleixq" />
      </p>
    </div>
    <div class="xqname">
      <p class="p2" v-html="n.name"></p>
      <p class="p3">{{ n.characteristic }}</p>
      <div class="dijia">
        <p class="p4">
          低价
          <span>￥{{ n.minPrice }}</span>
        </p>
        <p class="p5">原价￥{{ n.originalPrice }}</p>
        <p class="p6">库存{{ n.stores }}</p>
      </div>
    </div>
    <van-tabs @click="onClick">
      <van-tab title="商品介绍">
        <div class="data">
          <div v-html="goodstitle"></div>
        </div>
      </van-tab>
      <van-tab title="商品评价">
        <ul class="pj">
          <li v-for="(item) in pinjia" :key="item.id">
            <van-icon size="55" name="user-circle-o" />

            <div>
              <div>
                <p class="p7">{{ item.user.nick == null ?'匿名用户': item.user.nick}}</p>
                <p class="p8">{{ item.goods.goodReputationStr }}</p>
              </div>
              <p>{{ item.goods.goodReputationRemark }}</p>
              <div>
                <p class="p9">{{ item.goods.dateReputation }}</p>
                <p class="p10">
                  选择规格:
                  <span>{{ item.goods.property }}</span>
                </p>
              </div>
            </div>
          </li>
        </ul>
      </van-tab>
    </van-tabs>
    <van-popup v-model="show" closeable position="bottom">
      <div class="liebxq">
        <div class="lbxq1">
          <img :src="n.pic" />
          <div class="lbxq2">
            <p class="lbp0">{{ n.name }}</p>
            <p class="lbp1">￥{{ n.minPrice }}</p>
          </div>
        </div>
        <div class="lbxq3">
          <div class="lbxq3div" v-for="(p,index1) in d" :key="p.id">
            <p class="lbp2">{{ p.name }}</p>
            <div class="lbxq4">
              <div
                class="lbxq5 active2"
                :class="{'active1' :p2.checked}"
                @click="select(index1,index2)"
                v-for="(p2,index2) in p.childsCurGoods"
                :key="p2.id"
              >{{ p2.name }}</div>
            </div>
          </div>
        </div>
        <div class="gwsl">
          <p>购物数量</p>
          <van-stepper v-model="value" />
        </div>

        <p class="lbxqp" @click="jiaru">立即购买</p>
      </div>
    </van-popup>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" color="#07c160" />
      <van-goods-action-icon
        icon="cart-o"
        text="购物车"
        @click="gouwuche"
        :badge="$store.getters.nums"

      />
      <van-goods-action-icon v-if="hid" icon="star-o" text="收藏" color="#000" @click="shoucang" />
      <van-goods-action-icon v-else icon="star" text="已收藏" color="red" @click="shoucang" />
      <van-goods-action-button type="warning" text="加入购物车" @click="jiagouwu" />

      <van-goods-action-button type="danger" text="立即购买" @click="jiagouwu" />
      <!-- <van-popup v-model="show" closeable position="bottom" :style="{ height: '30%' }" /> -->
    </van-goods-action>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      f: false,
      banner: [],
      n: {},
      mc: ["商品介绍", "商品评价"],
      //商品详情
      goodstitle: "",
      pinjia: [],
      d: [],
      a:[],
      //点击购物车弹出层
      show: false,

      //收藏
      hid: false,
      value: 1
    };
  },
  created() {
    //创建后
  },
  components: {

  }, // 局部注册的组件
  mounted() {
    //加载后
    var id = this.$route.query.id;
    this.$http
      .get("https://api.it120.cc/small4/shop/goods/detail?id=" + id)
      .then(res => {
        // console.log(res);
        this.banner = res.data.data.pics;
        this.n = res.data.data.basicInfo;
        this.goodstitle = res.data.data.content;
        this.d = res.data.data.properties;
        this.a = res.data.data;
        // console.log(this.d);

        // 获取总数据
        let arr = res.data.data.properties;
        // console.log(arr);

        // 总数据循环
        arr.forEach(item => {
          item.childsCurGoods.forEach(a => {
            a.checked = false;
          });
        });

        this.d = arr;
      });
    //获取评价接口
    this.$http
      .post(
        "https://api.it120.cc/small4/shop/goods/reputation?goodsId=" +
          this.$route.query.id
      )
      .then(res => {
        if (res.data.msg == "success") {
          this.pinjia = res.data.data;
        }
      });
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    fenleixq() {
      this.$router.go(-1);
    },
    onClick(name, title) {
      //   Toast(title);
    },
    //点击购物车弹出层
    jiagouwu() {
      this.show = true;
    },
    gouwuche() {
      this.$router.push('/shop')
    },
    //收藏
    shoucang() {
      this.hid = !this.hid;
    },
    select(n1, n2) {
      this.d[n1].childsCurGoods.forEach((item, index) => {
        if (index == n2) {
          item.checked = true;
          this.$set(this.d[n1].childsCurGoods, index, item);
        } else {
          item.checked = false;
          this.$set(this.d[n1].childsCurGoods, index, item);
        }
      });
    },
    jiaru() {
      var t = localStorage.getItem("token");
      console.log(t);
      if (t) {
        //把数据添加到购物车
        var p0 = [];
        this.d.forEach(item => {
          var r = item.childsCurGoods.filter(p => {
            return p.checked;
          });
          p0 = p0.concat(r);
          // console.log(p0)
        });
        
        console.log(p0);
        if(p0.length <this.d.length){
          // alert("请选择属性")
          this.$notify('请输入属性');
          return false
        }

        // var obj = {
        //   pic: this.a.basicInfo.pic,
        //   name: this.a.basicInfo.name,
        //   price: this.a.basicInfo.minprice,
        //   num: this.value,
        //   pro: p0,
        //   checked: false,
        //   id: this.a.basicInfo.id
        //   // properties:this
        // };
        var obj= new Object
         obj.pic= this.a.basicInfo.pic,
          obj.name=this.a.basicInfo.name,
          obj.price=this.a.basicInfo.minPrice,
          obj.num=this.value,
          obj.pro=p0,
          obj.checked= false,
          obj.id=this.a.basicInfo.id
        console.log(this.a.basicInfo.minPrice)
        this.$store.commit("addCart", obj);
        this.$router.push("/home/shop")
        Toast.success('加入成功');
        this.show.false
      } else {
        this.$router.push("/denglu");
      }
    }
  }
};
</script>

<style  lang="less">
#spxq {
  .fan {
    width: 0.36rem;
    height: 0.36rem;
    padding: 0.03rem;
    border-radius: 50%;
    border: 0.02rem solid #fff;
    position: fixed;
    left: 0.1rem;
    top: 0.02rem;
    text-align: center;
    z-index: 1;
    .p1 {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      text-align: center;
      // background: chartreuse;
      background: white;
      z-index: 20;
      .van-icon {
        font-size: 0.3rem;
        color: #666;
        margin-top: 0.02rem;
        border-radius: 0.5rem;
      }
    }
  }
  //轮播
  .van-swipe {
    //  width: 100%;
    width: 4rem;
    overflow: hidden;
    margin: 0 auto;
    .van-swipe-item {
      width: 100%;

      > .lbimg {
        width: 100%;
        height: 4rem;
      }
    }
  }
  .xqname {
    //   width: 5rem;
    height: 1.24rem;
    background: #ffffff;
    padding-left: 0.2rem;
    border-bottom: 0.14rem solid #f5f5f5;
    .p2 {
      font-size: 0.22rem;
      color: #464646;
      margin-top: 0.3rem;
    }
    .p3 {
      font-size: 0.16rem;
      color: #b8b8b8;
      margin-top: 0.16rem;
    }
    .dijia {
      display: flex;
      margin-top: 0.2rem;
      .p4 {
        font-size: 0.14rem;
        color: #cc0c0c;
        width: 0.8rem;
        span {
          font-size: 0.2rem;
        }
      }
      .p5 {
        font-size: 0.14rem;
        color: #b8b8b8;
        margin-top: 0.06rem;
        width: 2.3rem;
      }
      .p6 {
        font-size: 0.14rem;
        color: #b8b8b8;
        margin-top: 0.06rem;
      }
    }
  }
  .data {
    width: 100%;
    //   overflow: hidden;
    ul {
      li {
        height: 0.47rem;
        line-height: 0.27rem;
        padding-left: 0.14rem;
        padding-right: 0.24rem;
        p {
          width: 100% !important;
          display: block !important;
        }
      }
    }

    div {
      img {
        width: 100% !important;
      }
    }
  }
  .pj {
    width: 100%;
    li {
      height: 1.54rem;
      // line-height: 1.54rem;
      padding: 0 0.2rem;
      border-bottom: 0.01rem solid #e9e9e9;
      display: flex;
      .van-icon {
        position: relative;
        display: inline-block;
        margin-top: 0.3rem;
      }
      // #i {
      //   width: 0.8rem;
      // }
      div {
        margin-left: 0.14rem;
        div {
          display: flex;
          align-items: center;
          margin-top: 0.18rem;
          .p7 {
            font-size: 0.16rem;
          }
          .p8 {
            width: 0.48rem;
            height: 0.25rem;
            border-radius: 0.06rem;
            color: white;
            text-align: center;
            line-height: 0.25rem;
            background: #b7282e;
            font-size: 0.16rem;
            margin-left: 0.2rem;
          }
        }
        div {
          display: flex;
          margin-top: 0.2rem;
          .p9 {
            font-size: 0.12rem;
            color: #b2b2b2;
          }
          .p10 {
            font-size: 0.12rem;
            color: #b2b2b2;
          }
        }
      }
    }
  }
  //已收藏
  .color1 {
    color: #ff5000;
  }

  //点击加入购物车
  .lbxq1 {
    height: 1.23rem;
    border-bottom: 0.01rem solid #e9e9e9;
    display: flex;
    img {
      width: 0.85rem;
      height: 0.85rem;
      display: block;
      margin-left: 0.2rem;
      margin-top: 0.2rem;
    }

    .lbxq2 {
      margin-left: 0.16rem;
      width: 3.5rem;
      .lbp0 {
        font-size: 0.17rem;
        margin-top: 0.3rem;
      }
      .lbp1 {
        font-size: 0.18rem;
        color: #d00000;
        margin-top: 0.24rem;
      }
    }

    .liebxq {
      background: red;
    }
  }

  .lbxq3 {
    padding: 0.3rem 0;
    border-bottom: 0.01rem solid #e9e9e9;
    // background: red;
    .lbxq3div {
      .lbp2 {
        font-size: 0.18rem;
        margin-left: 0.2rem;
      }
      .lbxq4 {
        display: flex;
        flex-wrap: wrap;
        .lbxq5 {
          padding: 0.1rem 0.2rem;
          border-radius: 0.06rem;
          margin-top: 0.1rem;
          margin-left: 0.1rem;
        }
      }
    }
  }
  .lbxqp {
    width: 100%;
    height: 0.4rem;
    text-align: center;
    line-height: 0.4rem;
    font-size: 0.2rem;
    color: white;
    background: #b7282e;
  }
  .gwsl {
    height: 0.98rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.2rem;
  }
  .active1 {
    border: 1px solid red !important;
  }
  .active2 {
    border: 0.01rem solid #ccc;
  }
}
</style>



