<template>
  <div class="dd">
    <van-nav-bar title="确认订单" left-arrow />
    <div class="dadad">
      <div class="dz">
        <p>
          <van-icon name="plus" />
        </p>
        <div class="xz">新增收货地址</div>
        <van-icon name="arrow" />
      </div>

      <p class="splb">商品列表</p>
      <ul>
        <li v-for="(item,index) in $store.getters.order" :key="index">
          <img :src="item.pic" alt />
          <div class="lidiv">
            <p class="lip1" v-html="item.name"></p>
            <p class="lip2">
              <span v-for="p in item.pro" :key="p.item">{{ p.name}}</span>
            </p>
          </div>
          <div class="lidiv1">
            <p class="lip3">￥{{ item.price }}</p>
            <p class="lip4">×{{ item.num }}</p>
          </div>
        </li>
      </ul>
      <div class="psfs">
        <p>配送方式</p>
        <p>快递</p>
      </div>
      <div class="bz">
        <span>备注</span>
        <input type="text" placeholder="如需备注请输入" />
      </div>
      <div class="spje">
        <p>商品金额</p>
        <p>￥{{ }}</p>
      </div>
    </div>
    <div class="hjdd">
      <p class="hjddp1">合计：￥{{ $store.getters.jg }}</p>
      <p class="hjddp2">提交订单</p>
    </div>
  </div>
</template>

<script>
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
    };
  },
  created() {
    //创建后
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
  }
};
</script>

<style scoped lang="less">

.dd {
  height: 100%;
  position: relative;
  height: 100%;
  margin-bottom: 1.4rem;
  .dz {
    display: flex;
    align-items: center;
    height: 1.1rem;
    padding: 0 0.2rem;
    > p {
      width: 0.6rem;
      height: 0.44rem;
      border-radius: 0.06rem;
      text-align: center;
      background: #3283f9;
      .van-icon {
        font-size: 0.2rem;
        color: white;
        margin-top: 0.13rem;
      }
    }
    > .xz {
      width: 3.8rem;
      height: 0.58rem;
      line-height: 0.58rem;
      margin-left: 0.32rem;
    }
    .van-icon {
      font-size: 0.24rem;
      color: #dbdadf;
    }
  }
  .splb {
    height: 0.65rem;
    padding: 0 0.2rem;
    line-height: 0.65rem;
    font-size: 0.16rem;
    border-top: 0.13rem solid #f5f5f5;
    border-bottom: 2px solid #e8e8e8;
  }
  ul {
    border-bottom: 0.15rem solid #f5f5f5;
    li {
      height: 1.53rem;
      border-bottom: 1px solid #e8e8e8;
      display: flex;
      align-items: center;
      img {
        width: 1.12rem;
        height: 1.12rem;
        display: block;
        margin-left: 0.2rem;
      }
      .lidiv {
        margin-left: 0.12rem;
        width: 3.06rem;
        height: 1.12rem;
      }
    }
  }
  .psfs {
    height: 0.6rem;
    padding: 0 0.2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 0.18rem;
    border-bottom: 0.01rem solid #e5e5e5;
  }
  .bz {
    height: 0.59rem;
    padding: 0 0.2rem;
    display: flex;
    align-items: center;
    span {
      font-size: 0.18rem;
    }
    input {
      width: 2.8rem;
      height: 0.2rem;
      font-size: 0.18rem;
      color: #333;
      margin-left: 0.3rem;
      border: none;
    }
  }
  .spje {
    height: 0.64rem;
    padding: 0 0.2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 0.15rem solid #f5f5f5;
    border-bottom: 0.15rem solid #f5f5f5;
  }
  .hjdd {
    width: 100%;
    background: white;
    //   height: 0.69rem;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .hjddp1 {
      font-size: 0.2rem;
      color: #d00000;
      line-height: 0.69rem;
      margin-left: 1rem;
    }
    .hjddp2 {
      width: 1.6rem;
      height: 0.69rem;
      background: #b7282e;
      color: white;
      font-size: 0.2rem;
      text-align: center;
      line-height: 0.69rem;
    }
  }
  .lidiv {
    margin-left: 0.12rem;
    width: 3.06rem;
    height: 1.12rem;
    .lip1 {
      font-size: 0.18rem;
      margin-top: 0.1rem;
    }
    .lip2 {
      font-size: 0.18rem;
      margin-top: 0.55rem;
      color: #999;
    }
  }
  .lidiv1 {
    height: 1.3rem;
    .lip3 {
      font-size: 0.18rem;
      margin-top: 0.1rem;
    }
    .lip4 {
      font-size: 0.18rem;
      margin-top: 0.6rem;
      margin-left: 0.1rem;
      color: #999;
    }
  }
}
</style>



