<template>
  <div>
    <h4>个人</h4>
    <button @click="quit()">退出</button>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {},
  methods: {
    quit(){
      localStorage.removeItem("token")
      this.$router.push("/denglu")
    }
  }
};
</script>

