<template>
  <div class="fenlei">
    <van-nav-bar :border="f" title="分类商品" left-arrow @click-left="onClickLeft" />

    <ul v-if="list.length != 0">
        <li v-for="item in list" :key="item.id" @click="spxq(item.id)">
            <img :src="item.pic" alt="">
            <p>{{ item.name }}</p>
            <p>￥{{ item.originalPrice }}</p>
        </li>
    </ul>
    <img v-else  src="static/imgs/meiyou.png" alt="">
    
  </div>
  
</template>

<script>
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
        list:[],
        f:false
    };
  },
  created() {
    //创建后
    // this.$router.query()
    this.$http.get("https://api.it120.cc/small4/shop/goods/list").then((res)=>{
        console.log(res)
        if(res.data.msg =="success"){
          this.list =  res.data.data.filter((item)=>{
                return item.categoryId == this.$route.query.cid
            })
        }
    })
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    onClickLeft() {
      this.$router.go(-1);
    },
    //跳转商品详情
    spxq(id){
        this.$router.push("/spxq?id="+id)
    }
  }
};
</script>

<style scoped lang="less">

.van-nav-bar .van-icon {
    color: #1989fa;
}
.fenlei{
    
    ul{
        // margin-top: 0.08rem;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 0.08rem;
    li{
        width: 1.9rem;
       
        position: relative;
        img{
            width: 100%;
        }
        p:nth-of-type(1){
            position: absolute;
            left: 0;
            top: 2rem;
            width: 100%;
            height: 0.4rem;
            line-height: 0.4rem;
            background: #F0EDD4;
            font-size: 0.12rem;
            color: #B69B72;
        }
         p:nth-of-type(2){
            position: absolute;
            left: 0;
            top: 2.5rem;
            font-size: 0.2rem;
            color: #CF1311;
        }
    }
    }
    img{
        width: 100%;
        margin-top: 0.9rem;
    }
}

</style>



