<template>
  <div>
    <van-nav-bar title="购物车" left-text="编辑" v-show="bj" @click-left="spbj" />
    <van-nav-bar title="购物车" left-text="完成" v-show="!bj" @click-left="spbj" />
    <ul class="shop">
      <li v-for="(item,index) in list" :key="index">
        <div class="lidiv">
          <!-- <van-checkbox v-model="item.checked" checked-color="#07c160" @click="sel(index)"></van-checkbox> -->
          <!-- <van-checkbox v-model="item.checked" @click="sel(index)"></van-checkbox> -->
          <input  v-model="item.checked" @click="sel(index)" type="checkbox">
          <img :src="item.pic" alt />
          <div class="spxx">
            <p v-html="item.name" class="spxxp1"></p>
            <p class="spxxp2" v-for="(v,k) in item.pro" :key="k">{{v.name}}</p>
            <div class="spxx1">
              <p class="spxx1p1">￥{{ item.price }}</p>
              <van-stepper v-model="item.num" @minus="jian(index)" @plus="jia(index)"/>
            </div>
          </div>
        </div>
      </li>
    </ul>

    <div class="hj">
      <!-- <van-checkbox @click="qx" v-model="checked" >全选</van-checkbox> -->
      <input type="checkbox" @click="qx" v-model="checked"> 全选
      <p class="hjp1" >合计：￥{{ this.$store.getters.jg }}</p>
      <p class="hjp2" v-show="bj" @click="xiadan">下单</p>
      <p class="hjp2" v-show="!bj" @click="del"  >删除</p>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      checked: false,
      value: 1,
      getlist: this.$store.state.cart,
      bj: true
    };
  },
  created() {
    //创建后
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  // 计算属性
  computed: {
    ...mapState({
      list: state => state.cart,
      t: state => sate.title
    })

    //   goods(){
    //           return this.$store.state.cart
    //   }
  },
  methods: {
    //方法集合
    spbj() {
      this.bj = !this.bj;
    },
    sel(n){
      // this.list[n].checked
      // this.$store.commit("",{index:n,value:this.list[n].checked})
      this.$store.commit("sel",n)
      this.checked= this.getlist.every((itme)=>{
        return itme.checked==true
      })
    },
    qx(){
      this.checked=!this.checked
      this.getlist.forEach((itme)=>{
        itme.checked=this.checked
      })
    },
    jia(k){
      this.$store.commit("jia",k)
    },
    jian(k){
      this.$store.commit("jian",k)
    },
    //删除
    del(){
    
      this.$store.commit("del")
    },
    //下单
    xiadan(){
      this.$router.push('/order')
    } 
  }
};
</script>

<style scoped lang="less">
.shop {
  //   padding-top: 0.57rem;
  width: 5.2rem;
  overflow: hidden;
  margin-bottom: 2rem;
  li {
    height: 1.6rem;
    display: flex;
    align-items: center;
    padding-left: 0.16rem;
    width: 5.78rem;
    position: relative;
    transition: all 0.5s;
    
    .lidiv {
      display: flex;
      img {
        width: 1.1rem;
        height: 1.1rem;
        display: block;
        border-radius: 0.04rem;
        margin-left: 0.17rem;
      }
      .spxx {
        width: 3.09rem;
        height: 1.1rem;
        margin-left: 0.24rem;
        .spxxp1 {
          font-size: 0.14rem;
        }
        .spxxp2 {
          font-size: 0.14rem;
          margin-top: 0.16rem;
          color: #b2b2b2;
        }
        .spxx1 {
          display: flex;
          align-items: center;
          margin-top: 0.17rem;
          width: 2rem;
          justify-content: space-between;
          .spxx1p1 {
            font-size: 0.18rem;
            color: #d00000;
          }
        }
      }
    }
  }
}
.hj {
  height: 0.68rem;
  border-top: 0.01rem solid #e9e9e9;
  border-bottom: 0.01rem solid #e9e9e9;
  display: flex;
  align-items: center;
  background: white;
  z-index: 1;
  justify-content: space-between;
  padding-left: 0.16rem;
  width: 100%;
  position: fixed;
  left: 0;
  bottom: 0.87rem;
  .hjp1{
    font-size: 0.18rem;
    color: #d00000;
  }
  .hjp2{
    width: 1.66rem;
    height: 0.68rem;
    font-size: 0.2rem;
    color: white;
    text-align: center;
    line-height: 0.68rem;
    background: #d00000;
  }
}
</style>



