<template>
  <div>
    <div class="bg">
      <p>全民砍价></p>
    </div>
    <div>
      <van-loading v-if="loading" type="spinner" size="24px"></van-loading>
      <ul v-else>
        <kjzi v-for="(item,index) in kj " :d="item"  v-show='index>2' :key="item.id"></kjzi>
      </ul>
    </div>
  </div>
</template>

<script>
import kjzi from "./Kjzi";
export default {
  data() {
    return {
      kj: [],
      loading: true
    };
  },
  components: {
    kjzi
  },
  created() {},
  mounted() {
    this.$http
      .get("https://api.it120.cc/small4/shop/goods/kanjia/list")
      .then(res => {
        console.log(res);
        // res.data.data.goodsMap;
        for(let i in res.data.data.goodsMap){
          this.kj.push(res.data.data.goodsMap[i])
        }
        this.loading = false;
      });
  },
  methods: {}
};
</script>

<style  lang="less">
</style>



