<template>
  <div class="newjx">
    <van-nav-bar title="专栏详情" left-arrow @click-left="onClickLeft" />
    <h3>{{ list.title }}</h3>
    <p class="newjxp1">
      <img :src="list.pic" alt />
    </p>
    <div class="newjx1" v-html="list.content">
            <p></p>
    </div>
  </div>
</template>

<script>
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      list: {}
    };
  },
  created() {
    //创建后
    //        this.$route.query
    //        console.log(this.$route.query)
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
    this.$http
      .get(
        "https://api.it120.cc/small4/cms/news/detail?id=" + this.$route.query.id
      )
      .then(res => {
        // console.log(res);
        console.log(this.$route.query.id);
        this.list = res.data.data;
      });
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped lang="less">
.newjx h3 {
  font-size: 0.3rem;
  color: #333333;
  width: 100%;
  text-align: center;
  margin-top: 0.45rem;
  margin-bottom: 0.2rem;
}
.newjxp1 {
  img {
    width: 4.85rem;
    height: 3.13rem;
    display: block;
    margin: 0 auto;
  }
}
.newjx1{
        font-size: 0.18rem;
    color: #333333;
    line-height: 0.48rem;
    text-indent: 2em;
    margin-top: 0.1rem;
}
</style>



