import Vue from "vue"
import Vuex from "vuex"
import createPersistedState from 'vuex-persistedstate'
Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        title:"hello",
        cart:[]
    },
    mutations:{
        addCart(state,goods){

            // state.cart.push(goods)

           var sp = state.cart.findIndex((item)=>{
                return item.id ==goods.id  
            })
            
            if(sp == -1){
                state.cart.push(goods)
            }else{
                var cart = state.cart
                cart[sp].num += goods.num
                state.cart =cart
            }
           console.log(state.cart)
        },
        sel(state,n){
            state.cart[n].checked=!state.cart[n].checked
        },
        //加
        jia(state,k){
            state.cart[k].num++
        },
        //减
        jian(state,k){
            state.cart[k].num--
        },
        //删除
        del(state){
           state.cart = state.cart.filter((item)=>{
                return !item.checked
            })
        }
    },
    actions:{},
    getters:{
        nums(state){
            var s =0
            state.cart.forEach((item)=>{
                s+=item.num
            })
            return s
        },
        jg(state){
            var s =0
            state.cart.forEach((item)=>{
                if(item.checked){
                    s+=item.num*item.price
                }
            })
            return s
        },
        order(state){
            return state.cart.filter((item)=>{
                console.log(item)
                return item.checked
            })
        }
    },
    moudel:{

    },
    plugins:[createPersistedState()]

})


