<template>
  <div class="zc">
    <div class="wrap">
      <h4>登录账号</h4>
      <p class="p">虾米-严选商城欢迎您</p>
      <p class="zcp1">
        <input type="text" placeholder="手机号" v-model="tel" @blur="sjh" />
        <span style="display:none">必填</span>
        <van-icon class="van" name="graphic" />
      </p>
      <p class="zcp1">
        <input type="password" placeholder="密码" v-model="pwd" v-if="yj" @blur="dlmima" />
        <input type="text" placeholder="密码" v-model="pwd" v-else />
        <van-icon class="van" name="closed-eye" v-if="yj" @click="mima" />
        <van-icon class="van" name="eye-o" v-else @click="mm" />
        <span style="display:none">必填</span>
      </p>
      <p class="ljdl" @click="denglu">登录</p>
      <p class="wjmm">忘记密码</p>
      <p class="pw">还没有注册？ 立即注册</p>
    </div>
  </div>
</template>

<script>
import qs from "qs";
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      //手机号
      tel: "",
      //密码
      pwd: "",

      yj: true,

      pp:""
    };
  },
  created() {
    //创建后
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
    
  },
beforeRouteEnter(to, from, next){
  console.log(from)
  next(vm =>{
    //通过“vm” 访问组件实例
    // vm.pp =from.path
    vm.pp =from.fullPath
  })
},
  
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    //立即登录
    denglu() {
      var data = { mobile: this.tel, pwd: this.pwd };

      this.$http
        .post(
          "https://api.it120.cc/small4/user/m/login?deviceId=007&deviceName=monkey",
          qs.stringify(data)).then((res) => {
          console.log(res);
          if (res.data.msg == "success") {
            localStorage.setItem("token", res.data.data.token)

              if(this.pp !=""){
                this.$router.push(this.pp)
              }else{
                this.$router.push("/home/wd")
              }

           
          }else{
              alert("用户名或密码不正确")
          }
        });
    },
    //密码隐藏
    mima() {
      this.yj = false;
    },
    //密码显示
    mm() {
      this.yj = true;
    },
    //失去焦点  手机号为空时 弹框
    sjh() {
      if (this.tel == "") {
        this.$notify({ type: 'warning', message: '请输入手机号' });
      }
    },
    //失去焦点  密码为空时 弹框
    dlmima() {
      if (this.pwd == "") {
        this.$notify({ type: 'warning', message: '请输入密码' });
      }
    }
    // yhmm(){
    //     if(this.yhmm==""){
    //         this.$notify('请输入用户名');
    //     }
    // }
  }
};
</script>

<style scoped lang="less">
.zc {
  padding: 0.2rem;
  .wrap {
    padding: 0 0.2rem;
    height: 10rem;
    min-height: 10rem;
    border: 0.02rem solid #f5f5f5;
    h4 {
      font-size: 0.3rem;
      margin-top: 0.4rem;
    }
    .p {
      font-size: 0.2rem;
      color: #909090;
      margin-top: 0.5rem;
      margin-bottom: 0.23rem;
    }
    .zcp1 {
      //   width: 2.8rem;
      height: 0.6rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.44rem;
      display: flex;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      input {
        margin-left: 0.2rem;
        width: 2.7rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        color: #bfbfbf;
      }
      span {
        font-size: 0.18rem;
        color: #e12c1e;
      }
      .van {
        font-size: 0.22rem;
        color: #d1cfce;
        position: absolute;
        left: 2.5rem;
        top: 0.24rem;
      }
    }
    .wjmm {
      font-size: 0.16rem;
      color: #989898;
      width: 100%;
      text-align: center;
      margin-top: 0.36rem;
    }
    .ljdl {
      height: 0.57rem;
      border-radius: 0.06rem;
      background: -webkit-gradient(
        linear,
        left top,
        right top,
        from(#5ea6f8),
        to(#bb87f6)
      );
      background: linear-gradient(90deg, #5ea6f8, #bb87f6);
      text-align: center;
      line-height: 0.57rem;
      font-size: 0.2rem;
      color: white;
      letter-spacing: 0.06rem;
      margin: 0 auto;
      margin-top: 0.24rem;
    }
    .pw {
      height: 0.5rem;
      margin-top: 0.2rem;
      font-size: 0.18rem;
      color: dodgerblue;
      text-align: center;
    }
  }
}
</style>



