<template>
  <div>
    <p class="seach">
      <input type="text" placeholder="回车搜索商品" />
    </p>
    <div class="fl0">
      <div class="fl">
        <ol>
          <li :class="{'active':num==0}" @click="all">所有分类</li>
          <li
            :class="{'active':(index+1)==num}"
            v-for="(item,index) in liebiao1"
            :key="index"
            @click="children(item.id,(index+1))"
          >{{ item.name }}</li>
        </ol>
      </div>
      <div class="fllb">
        <div class="lbt">
          <van-swipe class="my-swipe" :autoplay="3000">
            <van-swipe-item v-for="(item,index) in banners" :key="index">
              <img class="lbimg" :src="item.img" alt />
            </van-swipe-item>
          </van-swipe>
        </div>

        <ul>
          <li v-for="item in liebiao2" :key="item.id" @click="flxq(item.id)">
            <img :src="item.icon" alt />
            <p>{{ item.name }}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      liebiao1: [],
      liebiao2: [],
      liebiao3: [],
      num: 0,
      banners: [
        { img: "static/imgs/img1/tup1.jpg" },
        { img: "static/imgs/img1/tup2.jpg" },
        { img: "static/imgs/img1/tup3.jpg" },
        { img: "static/imgs/img1/tup4.jpg" }
      ]
    };
  },
  created() {
    this.data();
  },
  mounted() {},
  methods: {
    //获取分类数据的方法
    async data() {
      var lcj = await this.$http.get(
        "https://api.it120.cc/small4/shop/goods/category/all"
      );
      console.log(lcj);
      if (lcj.data.msg == "success") {
        //通过数组过滤方法 把一级分类过滤出来
        var c1 = lcj.data.data.filter(item => {
          return item.level == 1;
        });
        console.log(c1);
        //把一级分类放到liebiao1
        this.liebiao1 = c1;
        //把所有分类放到liebiao3
        this.liebiao3 = lcj.data.data;

        this.liebiao2 = lcj.data.data;
      }
    },
    //点击切换样式
    children(id, index) {
      // alert(id)
      var lcj2 = this.liebiao3.filter(item => {
        return item.pid == id;
      });
      console.log(lcj2);
      this.liebiao2 = lcj2;
      //把索引值赋值给num
      this.num = index;
    },
    all() {
      //右侧显示所有分类
      this.lcj2 = this.liebiao3;
      //设置激活样式
      this.num = 0;
    },
    flxq(id){
      this.$router.push("/fenleixq?cid="+id)
    }
  }
};
</script>

<style scoped lang="less">
.active {
  color: #d41515;
  border-left: 0.015rem solid red;
}
.seach {
  width: 100%;
  height: 0.5rem;
  border-bottom: 0.01rem solid #f3f3f3;
  display: flex;
  align-items: center;
  input {
    width: 3.47rem;
    height: 0.35rem;
    background: #f5f5f5;
    border: none;
    border-radius: 0.2rem;
    margin-left: 0.2rem;
    padding-left: 0.2rem;
  }
}
.fl {
  //   width: 100%;
  display: flex;
  ol {
    // width: 1.2rem;
    border-right: 0.01rem solid #e9e9e9;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0.51rem;
    overflow: auto;
    // background: white;
    z-index: 1;
    li {
      //   width: 1.27rem;
      padding: 0.1rem 0.2rem;
      height: 0.5rem;
      font-size: 0.16rem;
      text-align: center;
      line-height: 0.5rem;
      margin-top: 0.1rem;
    }
  }
}

.fllb {
  width: 2.9rem;
  position: fixed;
  left: 1.1rem;
  top: 0.51rem;
  overflow: auto;
//   background-color: red;
  height: 80%;
  > ul {
    // width: 2.8rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    box-sizing: border-box;
    li {
      padding: 0.08rem;
      text-align: center;
      > img {
        width: 0.76rem;
        height: 0.76rem;
        display: block;
      }
      p{
          font-size: 0.14rem;
          margin-top: 0.1rem;
      }
    }
    // width: 33%;
  }
  .lbt {
    width: 100%;
  }
  .van-swipe {
    //  width: 100%;
    width: 2.4rem;
    overflow: hidden;
    margin: 0 auto;
    .van-swipe-item {
      width: 100%;

      >.lbimg {
        width: 2.4rem;
        height: 1.5rem;
        border-radius: 0.1rem;
        margin-top: 0.1rem;
      }
    }
  }
}
</style>



